import Sketch from "react-p5";

export default function Background() {
  let waves = [];
  let N = 80;
  let xscale = 0.003;
  let yspeed = 0.001;
  let cnv;
  const setup = (p5, canvasParentRef) => {
    cnv = p5
      .createCanvas(p5.windowWidth, p5.windowHeight)
      .parent(canvasParentRef);
    p5.background("#FAF3EE");
    cnv.position(0, 0, "absolute");
    cnv.style("z-index", "-2");
    p5.strokeWeight(0.1);
    p5.stroke("#1D3557");
    p5.noFill();
  };

  const draw = (p5) => {
    p5.clear();
    for (var i = 0; i < N; i++) {
      let y_attn = p5.map(i, 0, N, p5.height * 0.001, p5.height * 0.3);
      waves.push(new NoisySine(xscale, yspeed, y_attn, i * 0.01));
    }

    for (var j = 0; j < N; j++) {
      waves[j].display(p5, 0, 9, 1);
      waves[j].display(p5, p5.PI / 2, 1.7, -1);
    }
  };

  const windowResized = (p5) => {
    p5.resizeCanvas(p5.windowWidth, p5.windowHeight);
  };

  function NoisySine(x_scale_, y_speed_, y_attn_, offset_) {
    this.N = 256;
    this.x_scale = x_scale_;
    this.y_speed = y_speed_;
    this.y_attn = y_attn_;
    this.offset = offset_;
  }

  NoisySine.prototype.display = function (p5, phase, height_div, dir) {
    p5.push();
    p5.translate(0, (0.95 * p5.height) / height_div + 0.05 * p5.width);
    p5.beginShape();
    for (var i = 0; i < this.N; i++) {
      let x = p5.norm(i, 0, this.N) * p5.width;
      let y_off = p5.lerp(
        -this.y_attn,
        this.y_attn,
        p5.noise(x * this.x_scale, p5.frameCount * this.y_speed + this.offset)
      );
      let y =
        (-p5.height / 24) * p5.sin(p5.norm(i, 0, this.N) * p5.TWO_PI + phase) +
        dir * y_off;
      p5.curveVertex(x, y);
    }
    p5.endShape();
    p5.pop();
  };
  return <Sketch setup={setup} draw={draw} windowResized={windowResized} />;
}
