import { Container, List, Item, LinkExt } from "./styles/socials";
import { Link } from "react-router-dom";
export default function Socials({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Socials.List = function SocialsList({ children, ...restProps }) {
  return <List {...restProps}>{children}</List>;
};

Socials.ItemRight = function SocialsItem({
  href,
  src,
  children,
  ...restProps
}) {
  return (
    <LinkExt href={href} target="_blank" rel="noreferrer">
      <Item src={src} />
      {children}
    </LinkExt>
  );
};

Socials.ItemLeft = function SocialsItem({ href, src, children, ...restProps }) {
  return (
    <LinkExt href={href} target="_blank" rel="noreferrer">
      {children}
      <Item src={src} />
    </LinkExt>
  );
};

Socials.LocalItem = function SocialsItem({ to, src, children, ...restProps }) {
  return (
    <Link style={{ textDecoration: "none" }} to={to}>
      <LinkExt>
        <Item src={src} />
        {children}
      </LinkExt>
    </Link>
  );
};
