import {
  Container,
  Text,
  Title,
  Email,
  SubText,
  Box,
  Sep,
} from "./styles/home";

export default function Home({ children, ...restProps }) {
  return <Container {...restProps}>{children}</Container>;
}

Home.Email = function HomeEmail({ href, children, ...restProps }) {
  return (
    <Email href={href} {...restProps}>
      {children}
    </Email>
  );
};

Home.Text = function HomeText({ children, ...restProps }) {
  return <Text {...restProps}>{children}</Text>;
};

Home.Title = function HomeText({ children, ...restProps }) {
  return <Title {...restProps}>{children}</Title>;
};

Home.SubText = function HomeSubText({ children, ...restProps }) {
  return <SubText {...restProps}>{children}</SubText>;
};

Home.Box = function HomeBox({ children, ...restProps }) {
  return <Box {...restProps}>{children}</Box>;
};

Home.Sep = function HomeSep({ children, ...restProps }) {
  return <Sep {...restProps}>{children}</Sep>;
};
